<template>
<main>
<!-- مدال ثبت اطلاعات -->
<div class="modal fade"  ref="staticBackdrop0" data-bs-backdrop="static" data-bs-keyboard="false" tabindex="-1" >
  <div class="modal-dialog  modal-dialog-centered" >
    <div class="modal-content" style="background-color:#F0FFFF;border:2px solid #2DA839">
      <div class="modal-header" style="border: none;">
        <img src="../assets/check.png" alt="" style="margin:auto;width:50px;height:50px;">
      </div>
      <div class="modal-body" style="margin:auto;border:none;font-size:1.4rem"> 
        {{modalBody}}
      </div>
      <div class="modal-footer " style="margin: auto;border:none">
        <button type="button" class="btn btn-primary mx-3" data-bs-dismiss="modal">بستن</button>
      </div>
    </div>
  </div>
</div>
<!-- مدال مشاهده قطعات -->
<div class="modal fade"  ref="staticBackdrop1" data-bs-backdrop="static" data-bs-keyboard="false" tabindex="-1" >
  <div class="modal-dialog  modal-dialog-centered" >
    <div class="modal-content"  id="modal-content"  style="background-color:#FFFFFF;border:2px solid #3563B2">
      <div class="modal-header" style="border: none;margin:auto;">
        <h4>محتویات ست شماره 1</h4> 
      </div>
      <div class="modal-body"  id="modal-body"  style="margin:auto;border:none;font-size:1.4rem"> 
            <table class="table  table-striped">
                    <tbody>
                        <tr>
                        <td>Mark</td>
                        </tr>
                        <tr>
                        <td>Mark</td>
                        </tr>
                        <tr>
                        <td>Mark</td>
                        </tr>
                        <tr>
                        <td>Mark</td>
                        </tr>
                        
                    </tbody>
            </table>
      </div>
      <div class="modal-footer " style="margin: auto;border:none">
        <button type="button" class="btn btn-primary mx-3" data-bs-dismiss="modal">بستن</button>
      </div>
    </div>
  </div>
</div>



<div id="btn">
        <button class="btn btn-success"  @click="showModal0" type="button" data-bs-toggle="modal" data-bs-target="#staticBackdrop0">ثبت اطلاعات</button>
        <button class="btn btn-primary"  @click="showModal1"  data-bs-toggle="modal" data-bs-target="#staticBackdrop1"  type="button">مشاهده قطعات</button>
        <button class="btn btn-primary"  type="button">چاپ بارکد</button>
</div>

</main>
</template>

<script>
import {Modal} from 'bootstrap';
export default {
    name:'ThreeModal',
    props:{
        modalBody:{type:String},
    },
    data() {
        return {
            modal:null,
        }
    },
    methods: {
      
      showModal0(){
        this.modal=new Modal(this.$refs.staticBackdrop0)  
        this.modal.show()
      },
      showModal1(){
        this.modal=new Modal(this.$refs.staticBackdrop1)  
        this.modal.show()
      },
    }
}
</script>

<style lang="scss" scoped>
#modal-content{
    
    #modal-body{
      height: 400px;
      width: 90%;
      overflow-y: scroll;  
        td{
            text-align: center;
        }
    }
}
#btn{
    
    text-align: center;
    margin: 1rem;
    button{
        margin: 0rem 2rem;
    }
}
</style>